import { Stack, Typography } from '@mui/material';
import { NextSeo } from 'next-seo';
import React, { useContext } from 'react';
import Tabber from '@components/common/Tabber';
import Market from '@components/account/Worlds/World6/farming/Market';
import { AppContext } from '@components/common/context/AppProvider';
import Plot from '@components/account/Worlds/World6/farming/Plot';
import Crop from '@components/account/Worlds/World6/farming/Crop';
import { CardTitleAndValue } from '@components/common/styles';
import { notateNumber } from '@utility/helpers';
import RankDatabase from '@components/account/Worlds/World6/farming/RankDatabase';
const Farming = () => {
  const {
    state
  } = useContext(AppContext);
  const {
    market,
    plot,
    crop,
    maxTimes,
    cropDepot = {},
    instaGrow,
    beanTrade,
    ranks,
    totalPoints,
    usedPoints
  } = state?.account?.farming || {};
  return <>
    <NextSeo title="Farming | Idleon Toolbox" description="Keep track of your garden with all its bonuses" data-sentry-element="NextSeo" data-sentry-source-file="farming.jsx" />
    <Stack direction={'row'} gap={1} data-sentry-element="Stack" data-sentry-source-file="farming.jsx">
      <CardTitleAndValue title={'Bean Trade'} value={Math.round(beanTrade)} icon={'data/Quest80_x1.png'} imgStyle={{
        width: 24
      }} data-sentry-element="CardTitleAndValue" data-sentry-source-file="farming.jsx" />
      <CardTitleAndValue title={'Insta Grow'} value={instaGrow} data-sentry-element="CardTitleAndValue" data-sentry-source-file="farming.jsx" />
      <CardTitleAndValue title={'Ranks pts'} value={`${usedPoints}/${totalPoints}`} data-sentry-element="CardTitleAndValue" data-sentry-source-file="farming.jsx" />
    </Stack>
    <Typography variant={'h5'} data-sentry-element="Typography" data-sentry-source-file="farming.jsx">Crop depot</Typography>
    <Stack direction={'row'} gap={1} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="farming.jsx">
      {Object.entries(cropDepot).map(([stat, {
        name,
        value
      }], index) => {
        const isMulti = stat === 'gamingEvo' || stat === 'cookingSpeed';
        const isBase = stat === 'critters';
        const val = notateNumber(value, isMulti ? 'MultiplierInfo' : 'Big');
        return <CardTitleAndValue key={stat} title={name} value={`${isBase ? '+' : ''}${val}${isBase ? '' : isMulti ? 'x' : '%'}`} icon={`etc/Pen_${index}.png`}>
        </CardTitleAndValue>;
      })}
    </Stack>
    <Tabber tabs={['Plot', 'Market', 'Rank database', 'Crop']} data-sentry-element="Tabber" data-sentry-source-file="farming.jsx">
      <Plot plot={plot} crop={crop} market={market} ranks={ranks} lastUpdated={state?.lastUpdated} account={state?.account} data-sentry-element="Plot" data-sentry-source-file="farming.jsx" />
      <Market market={market} crop={crop} data-sentry-element="Market" data-sentry-source-file="farming.jsx" />
      <RankDatabase ranks={ranks} data-sentry-element="RankDatabase" data-sentry-source-file="farming.jsx" />
      <Crop crop={crop} maxTimes={maxTimes} data-sentry-element="Crop" data-sentry-source-file="farming.jsx" />
    </Tabber>
  </>;
};
export default Farming;